<template>
  <div>

    <el-container height="100%">
      <el-header class="navHeader">
        <div class="title">系统管理<span class="subTitle">v1.0.0.0</span></div>
        
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link userTitle">
            {{username}}<i class="el-icon-user el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="logout">注销登录</el-dropdown-item>
            <el-dropdown-item command="lock">锁定</el-dropdown-item>

          </el-dropdown-menu>
        </el-dropdown>
      </el-header>
      <el-container>
        <el-aside
          width="auto"
          @mouseenter.native="collapseOpen"
          @mouseleave.native="collapseClose"
        >

          <el-menu
            router
            :unique-opened="true"
            :default-active="navselected"
            :active="navselected"
            @select="selectItems"
            :collapse="isCollapse"
          >
            <template v-for="(item) in this.activMen">
              <el-submenu
                v-if="item.meta.children && item.meta.children.length > 0"
                :index="item.meta.title"
                :key="item.meta.title"
              >
                <template slot="title"><i :class="item.meta.icon"></i><span>{{item.meta.title}}</span></template>

                <el-menu-item
                  v-for="(subItem,index1) in item.meta.children"
                  :index="subItem.path"
                  :key="index1"
                >
                  <i :class="subItem.meta.icon"></i><span>{{subItem.meta.title}}</span>
                </el-menu-item>

              </el-submenu>

              <el-menu-item
                v-else
                :index="item.path"
                :key="item.meta.path"
              ><i :class="item.meta.icon"></i><span>{{item.meta.title}}</span></el-menu-item>
            </template>
          </el-menu>

        </el-aside>

        <el-main>

          <router-view />

        </el-main>

      </el-container>
    </el-container>

  </div>
</template>

<script>
import { showMenuArr } from "../store";

export default {
  name: "Home",
  data() {
    return {
      username: this.$route.query.name,
      navselected: "/homePage",
      isCollapse: true,
    };
  },
  computed: {
    activMen: function () {
      let data = [];

      showMenuArr.forEach((element) => {
        if (element.meta.hidden == undefined) {
          data.push(element);
        }
      });
      return data;
    },
  },
  methods: {
    handleCommand(command) {
      if (command == "logout") {
        this.$httpApi.logout_post();
        this.$router.replace("/");
        this.$store.commit("resetRoutes");
      } else {
        //lock
        this.$httpApi.logout_post();
        this.$router.push("/Lock");
      }
    },
    selectItems(index) {
      this.$store.state.adminleftnavnum = index;
      //按钮选中之后设置当前的index为store里的值。
    },
    getNavType() {
      this.navselected = this.$store.state.adminleftnavnum;
      //store.state.adminleftnavnum里值变化的时候，设置navselected
    },
    collapseOpen() {
      this.isCollapse = false;
    },
    collapseClose() {
      
      if (this.$route.path != "/homePage") {
        this.isCollapse = true;
      }
    },
  },
  watch: {
    // 监测store.state
    "$store.state.adminleftnavnum": "getNavType",
  },
  mounted() {
    //菜单栏配置
    // console.log('load menu' + this.$route.query.level);
    this.navselected = this.$store.state.adminleftnavnum;
  
    this.isCollapse = this.navselected != "/homePage";

    this.username = window.sessionStorage.getItem("name");
    // console.log( this.navselected);
    this.$router.push({ path: this.navselected });
  },
};
</script>

<style>
.navHeader {
  background-color: #409eff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  box-sizing: border-box;
}
.navHeader .title {
  font-size: 30px;
  color: white;
  font-family: 华文楷体;
}
.navHeader .subTitle {
  padding-left: 10px;

  
  font-size: 20px;
  font-style: italic;
  color: white;
  font-family: 华文楷体;
}

.navHeader .userTitle {
  color: white;
}
.homeTitle {
  font-size: 50px;
  text-align: center;
  color: #409eff;
  margin: 0px auto;
  box-sizing: border-box;
}
.el-aside {
  height: 100vh;
}
.el-menu {
  border-right-width: 0;
}
</style>